@font-face {
    font-family: 'Basier Square';
    src: url("../../fonts/basiersquare-regular-webfont.woff2") format("woff2"), url("../../fonts/basiersquare-regular-webfont.woff") format("woff");
    font-weight: 400;
    font-style: normal;
}

@font-face {
    font-family: 'Cerebri Sans';
    src: url("../src/assets/fonts/Cerebri.Sans.Pro-www.Dfonts.org/Cerebri.Sans.Pro/CerebriSansPro-Bold.ttf") format("woff2"), url("../src/assets/fonts/Cerebri.Sans.Pro-www.Dfonts.org/Cerebri.Sans.Pro/CerebriSansPro-Bold.otf") format("woff");
    font-weight: 400;
    font-style: normal;
}

@font-face {
    font-family: 'Neuzeit Sans';
    src: url("../src/assets/fonts/Cerebri.Sans.Pro-www.Dfonts.org/Cerebri.Sans.Pro/CerebriSansPro-Bold.ttf") format("woff2"), url("../src/assets/fonts/Neuzeit_ff/Neuzeit\ Grotesk\ W01\ Regular.otf") format("woff");
    font-weight: 400;
    font-style: normal;
}

:root {
    --font-base: 'futura-pt', sans-serif;
    --font-alt: 'Basier Square', sans-serif;

    --font-cerebri: 'Cerebri Sans', sans-serif;
    --font-neuzeit: 'Neuzeit Sans', sans-serif;


    --color-primary: #e7a285;
    --footer-bg: #40383C;

    --text-black: #443227;
    --text-white: #FFFFFF;
    --text-grey: #ffffff99;
    --text-dark-grey: #5B6674;
    --text-dark: #181b31;
    --text-gray: #797b86;
    --header-dark: #060221;
    --text-dark-gray: #828282;
    --text-blue: #4762FF;
}

*,
html,
body {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    scroll-behavior: smooth;
}

a {
    color: unset;
    text-decoration: none;
}


.slide-bottom {
    -webkit-animation: slide-bottom 0.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
    animation: slide-bottom 0.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
}

@-webkit-keyframes slide-bottom {
    0% {
        -webkit-transform: translateY(-25%);
        transform: translateY(-25%);
    }

    100% {
        -webkit-transform: translateY(0);
        transform: translateY(0);
    }
}

@keyframes slide-bottom {
    0% {
        -webkit-transform: translateY(-25%);
        transform: translateY(-25%);
    }

    100% {
        -webkit-transform: translateY(0);
        transform: translateY(0);
    }
}